.properties {
    background-color: white;
    display: flex;
  }
  
  .card {
    width: calc(25% - 20px); /* Set width for each card, considering margin */
    background-color: rgb(196, 196, 220);
    padding: 10px; /* Adjust padding */
    margin: 10px; /* Adjust margin */
    border-radius: 10px;
  }
  
  .card-title {
    font-size: 18px;
    font-weight: bold;
    margin-bottom: 10px;
  }
  
  .card-text {
    font-size: 14px;
    line-height: 1.5;
  }
  